import React from 'react'
import Link from 'next/link'
import ContainerIcon from './blocks/ContainerIcon'
import LogoIcon from './icons/misc/logohome'

const Header = () => {

    return (
    
    <div className="header">


    
    
    <div className="section-wallet">
        w
    </div>


    <div className="section-center">
        <Link href="/">
            <ContainerIcon><LogoIcon /></ContainerIcon>
        </Link>
    </div>

    <div className="section-search">
        s
    </div>

    
    <style jsx>
        {`
        .section-wallet {
            display: flex;
            flex-direction: row;
        }
        .div-score {
            align-items: center;
            display: flex;
            justify-content: flex-end;padding
            padding-right: 7px;
            margin-left: 13px;

            width: 117px;
            /*height: 40px;*/
            border: 1px solid #225755;
            border-radius: 5px;
            opacity: 0.9;
        }
        .div-wallet {
            align-items: center;
            display: flex;
            justify-content: flex-end;
            padding-right: 7px;
            /*margin-left: 3px;*/
            width: 117px;
            /*height: 40px;*/
            border: 1px solid #AFC2C1;
            border-radius: 0px 5px 5px 0px;
            opacity: 0.9;
        }
        .header {
            height: 100%;
            align-items: center;
            display: flex;
            flex-direction: row;
            justify-content: space-between;
            margin: 0px;
        }
        .wallet-button {
            width: 48px;
            height: 40px;
            background: #255755 0% 0% no-repeat padding-box;
            border: 1px solid #225755;
            border-radius: 5px 0px 0px 5px;
        }
        .logo-button {
            width: 40px;
            height: 40px;
            border: 1px solid #225755;
            border-radius: 5px;
            opacity: 1;
        }

        .text {
            font: bold 16px/15px Arial;
            letter-spacing: 0;
            color: #000000;
            opacity: 0.9;
        }
        .label {
            margin-left: 2px;
            font: normal 10px/15px Arial;
            letter-spacing: 0;
            color: #255755;
            opacity: 0.4;
        }

        .header {
            margin-left: var(--main-margin);
        }
    `}



    </style>
    
    
    
    </div>
    )


}

export default Header;